.Tab{
  width: 1320px;
  height: 300px;
  border: 0.5px solid #eee;
  margin:0 auto;
  padding: 10%;  
}

.start{
  opacity: 0;
}
.end{
  opacity: 1;
  transition: opacity 0.5s;

}
.before{
  transform: scale(0);
}
.after{
  transform: scale(1);
  transition: all 0.5s;
}

